import React, { useState, useEffect } from "react";
import personServer from "./services/phoneNumber";
import Filter from "./Filter";
import PersonForm from "./PersonForm";
import Persons from "./Persons";
import Success from "./components/Success";
import "./css/index.css";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [filterKeyName, setfilterKeyName] = useState("");
  const [info, setInfo] = useState({ personName: "", number: "" });
  const [isSuccess, setIsSuccess] = useState(false);
  useEffect(() => {
    personServer
      .getAll()
      .then((res) => setPersons(res.data))
      .then(() => setTimeout(() => setIsSuccess(false), 3000));
  }, [info]);

  const handleFilterChange = (event) => {
    setfilterKeyName(event.target.value);
  };

  const handleUpdata = (personName, number) => {
    if (personName || number) {
      setInfo({ personName, number });
      setIsSuccess(true);
    }
  };

  const handleDeleteUpdate = () => {
    setInfo({})
  }
  return (
    <div>
      <h2>Phonebook</h2>
      {isSuccess ? <Success personName={info.personName} /> : ""}
      <Filter
        filterKeyName={filterKeyName}
        handleFilterChange={handleFilterChange}
      />

      <h2>add a new</h2>
      <PersonForm handleUpdata={handleUpdata} />
      <h2>Numbers</h2>
      <Persons
        persons={persons}
        filterKeyName={filterKeyName}
        handleDeleteUpdate={handleDeleteUpdate}
      />
    </div>
  );
};

export default App;
